.player {
    background: linear-gradient(to top, $orange, $yellow);

    display: flex;
    justify-content: center;
    flex-direction: column;

    padding: 0 15px;
    height: calc(100vh - 70px);

    &__main {
        display: flex;
        align-items: center;
        justify-content: center;

        margin-top: auto;
    }

    &__previous-song {
        display: none;
        max-width: 25%;
    }

    &__next-song {
        display: none;
        max-width: 25%;
    }

    &__current-song {
        display: flex;
        align-items: center;
        flex-direction: column;

        margin: 0 50px;
    }

    &__btn {
        background-color: transparent;
    }

    &__icon {
        width: 100px;
        height: 100px;
        fill: #fff;
    }

    &__icon-play {
        display: block;
    }

    &__icon-pause {
        display: none;
    }

    &__volume {}

    &__genre {
        overflow-x: scroll;
        scroll-snap-type: x mandatory;

        display: flex;
        flex-wrap: nowrap;

        padding: 0;
        margin: auto 0 35px;

        &-item {
            display: flex;
            align-items: center;
            justify-content: center;
            flex-direction: column;

            margin: 0 10px 15px 0;

            min-width: 90px;
            min-height: 90px;
            border-radius: 3px;
            background-color: #fff;

            text-align: center;
        }

        &-link {
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            width: 100%;
            height: 100%;

            text-decoration: none;

            opacity: .5;
            transition: .5s;

            &:hover {
                opacity: 1;
            }
        }

        &-item-play {
            width: 40px;
            height: 40px;
            fill: $orange;
        }

        &-item-title {
            font-weight: 500;
            font-size: 14px;
            margin-top: 5px;
            color: $orange;
        }
    }
}

.station {
    // z-index: 99;
    position: relative;
    width: 100%;
    height: 15%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    cursor: pointer;
    box-sizing: border-box;
}

.title {
    display: flex;
    font-size: 2vw;
    color: #fff;
    text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.33);
}

.subtitle {
    display: inline-block;
    vertical-align: middle;
}

@include rmin($sm-phone) {
    .player {
        &__genre {
            margin: auto 0 45px;

            &-item {
                min-width: 100px;
                min-height: 100px;
            }
        }
    }
}

@include rmin($tablet) {
    .player {
        padding: 0;
        height: calc(100vh - 100px);

        &__previous-song {
            display: block;
            text-align: center;
            margin-left: 25px;

            & h2 {
                color: #fff;
                font-size: 16px;
                // font-weight: 700;
            }

            & h3 {
                color: #fff;
                font-size: 16px;
                // font-weight: 700;
            }

            & span {
                color: #3b3b3b;
                font-size: 14px;
            }
        }

        &__current-song {
            margin: 0 75px;
        }

        &__next-song {
            display: block;
            text-align: center;
            margin-right: 25px;
            font-size: 16px;

            & h2 {
                color: #fff;
                font-size: 16px;
                // font-weight: 700;
            }

            & h3 {
                color: #fff;
                font-size: 16px;
                // font-weight: 700;
            }

            & span {
                color: #3b3b3b;
                font-size: 14px;
            }
        }

        &__genre {
            overflow: hidden;

            display: flex;
            justify-content: center;

            padding: 0;
            margin: auto 0 25px;

            &-item {
                margin: 15px 7px 0;
            }

            &-item-title {
                font-size: 16px;
            }
        }
    }
}

@include rmin($desktop) {
    .player {

        &__genre {
            &-item {
                position: relative;
                margin: 7px 15px 10px;

                min-width: 125px;
                min-height: 125px;

                &:hover {
                    bottom: 3px;
                    box-shadow: 0 4px 15px rgba(134, 72, 0, 0.4);
                }
            }

            &-item-play {
                width: 50px;
                height: 50px;
            }
        }
    }
}

